
@use "sass:math";
@use "@/assets/scss/_base";

:deep(.main) {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fps,
.chart-title {
    position: fixed;
    z-index: 1;
    top: 0;
    background: v-bind("moduleConfig.stageBackgroundColor");

    @include base.column-padding;
}

.fps {
    left: 0;
}

.stage-container {
    display: flex;
    min-width: calc(v-bind("moduleConfig.mainLayer.noAircraftsMessage.width") * 1px);
    min-height: 300px;
    align-items: center;
    justify-content: center;

    @media not all and (any-pointer: coarse) {
        width: 60vw;
        width: 60svw;
        height: 60vh;
        height: 60svh;
    }

    @media (any-pointer: coarse) {
        width: 90vw;
        width: 90svw;
        height: 80vh;
        height: 80svh;
    }
}
